<template>
  <div>
    <h4>评价管理</h4>
    <div>
      <el-form label-width="80px" :model="form" size="mini" inline>
        <el-form-item label="商品名称">
          <el-input v-model="form.prodName" />
        </el-form-item>
        <el-form-item label="订单编号">
          <el-input v-model="form.orderCode" />
        </el-form-item>
        <el-form-item label="买家昵称">
          <el-input v-model="form.name" />
        </el-form-item>
        <el-form-item label="评价时间">
          <el-date-picker v-model="form.commentTime" type="date" placeholder="选择日期" style="width: 178px"></el-date-picker>
        </el-form-item>
        <el-form-item>
          <el-button class="inquire1" type="success" style="width: 80px" @click="onSearch">查询</el-button>
          <el-button style="width: 80px" @click="onReset">重置</el-button>
        </el-form-item>
      </el-form>
    </div>
    <div class="comment_type">
      <el-form size="mini" label-width="80px">
        <el-form-item label="评价类型">
          <el-radio-group v-model="commentType" @change="onCommentType">
            <el-radio label="好评" />
            <el-radio label="中评" />
            <el-radio label="差评" />
          </el-radio-group>
        </el-form-item>
      </el-form>
    </div>

    <el-card style="min-width: 1100px">
      <el-table :data="commentList" style="width: 100%">
        <el-table-column label="评价" width="80">
          <template slot-scope="scope">
            <!-- 评价类型 0-不满意 1-一般 2-满意 -->
            <div v-if="scope.row.type === 2">
              <p class="iconfont">&#xe607;</p>
              <p>好评</p>
            </div>
            <div v-else-if="scope.row.type === 1">
              <p class="iconfont">&#xe600;</p>
              <p>中评</p>
            </div>
            <div v-else>
              <p class="iconfont">&#xe625;</p>
              <p>差评</p>
            </div>
          </template>
        </el-table-column>
        <el-table-column prop="orderId" label="订单号" width="180" />
        <el-table-column label="评价内容" show-overflow-tooltip>
          <template slot-scope="scope">
            <div class="comment_group">
              <p class="m-0">{{ scope.row.evaluate }}</p>
              <p class="comment_time m-0">初次评价: {{ scope.row.createTime }}</p>
              <el-popover v-if="scope.row.reply" class="reply" placement="top-start" title="标题" width="100" trigger="hover" :content="scope.row.reply">
                <el-button slot="reference" type="danger" size="mini">已回复</el-button>
              </el-popover>
            </div>
            <el-divider class="mt-1 mb-1" />
            <div v-if="scope.row.addEvaluate" class="comment_group">
              <p class="m-0">[追加评论] {{ scope.row.addEvaluate }}</p>
              <p class="comment_time m-0">追加评价: {{ scope.row.createTime }}</p>
              <el-popover v-if="scope.row.addReply" class="reply" placement="top-start" title="标题" width="100" trigger="hover" :content="scope.row.addReply">
                <el-button slot="reference" type="danger" size="mini">已回复</el-button>
              </el-popover>
            </div>
          </template>
        </el-table-column>
        <el-table-column label="商品信息" show-overflow-tooltip>
          <template slot-scope="scope">
            <p style="color: #329fec">{{ scope.row.prodName }}</p>
            <p style="color: #f6a337">￥188</p>
          </template>
        </el-table-column>
        <el-table-column label="操作" width="100">
          <template slot-scope="scope">
            <el-button type="primary" size="small" @click="onReply(scope)">查看全部</el-button>
          </template>
        </el-table-column>
      </el-table>
      <!-- 分页 -->
      <el-pagination background layout="prev, pager, next" :total="commentTotle" @current-change="handleCurrentChange" />
    </el-card>
  </div>
</template>

<script>
export default {
  name: 'EvaluationOrderList',

  data() {
    return {
      commentTime: '', // 评价时间
      commentType: '', // 评价类型
      pageNo: 1, // 分页的当前页数默认第一页为1
      commentTotle: 0, // 总页数
      commentList: [], // 评论列表
      form: {}, // 搜索表单
    }
  },

  mounted() {
    this._getCommentList()
  },

  methods: {
    // 评论列表分页
    handleCurrentChange(val) {
      this.pageNo = val
      this._getCommentList()
    },
    // 点击查看全部
    onReply(scope) {
      this.$router.push({ path: '/adminOrder/evaluationOrderList/content', query: { id: scope.row.id }})
    },
    // 点击搜索
    onSearch () {
      this._getCommentList()
    },
    // 点击重置
    onReset () {
      this.form = {}
    },
    // 点击评价列表
    onCommentType () {
      this._getCommentList()
    },

    // api------------------------------------------------------
    // 获取评论列表
    _getCommentList() {
      const DATA = {
        pageNo: this.pageNo,
        type: this.commentType,
        ...this.form
      }
      this.$http.post('/woho/order/evaluate/queryList', DATA)
        .then((res) => {
          this.commentList = res.data.data.list
          this.commentTotle = res.data.data.totle
        })
    }
    // api------------------------------------------------------
  }
}
</script>

<style lang="scss" scoped>
  .box-card {
    min-width: 1100px;
    margin-top:15px;
  }
  .comment_type{
    margin: 15px;
  }
  .iconfont{
    margin: 5px 0px 0px 0px;
    font-size: 30px;
    color: #ff632d;
  }
  .comment_group{
    position: relative;
    .comment_time{
      font-size: 13px;
      color: #999999;
    }
    .reply{
      position: absolute;
      top: 0px;
      right: 0px;
    }
  }
</style>
